      h2 1.1 La democracia

    p Trmino referido para designar a una de las formas de gobierno en que puede ejercerse el poder poltico del y para el pueblo, siendo la democracia la forma de gobierno de las mayoras o los muchos, a diferencia de las monarquas o las aristocracias.

    p.mb-5 Es entonces que, segn Hoyos Morales (como se cit en Pares Fundacin pares y reconciliacin, 2019). 

