    p.mb-3 La Declaracin Universal de los Derechos Humanos es un documento que marca un hito en la historia, elaborada por representantes de todas las regiones del mundo con diferentes antecedentes jurdicos y culturales. Fue proclamada por la Asamblea General de las Naciones Unidas en Pars, el 10 de diciembre de 1948 como un ideal comn para todos los pueblos y naciones. 

    .row.justify-content-center.align-items-center.mb-4
