        .custom_box_right    
          span.etiqueta--black En Colombia la Corte Constitucional en la sentencia T-401 de 1992 plantea en sus  fundamentos jurdicos,  que la dignidad humana
          span.etiqueta--red <b>[] es el presupuesto esencial de la consagracin y efectividad del sistema de derechos y garantas contemplado en la Constitucin y se la considera como principio fundante del Estado, que tiene valor absoluto no susceptible de ser limitado ni relativizado bajo ninguna circunstancia,[de manera que] () el respeto de la dignidad humana debe inspirar todas las actuaciones del Estado [] </b>
          span.etiqueta--black y, por tanto, la integridad del ser humano se constituye en razn de ser, principio y fin ltimo de la organizacin estatal.
