    #t_1_3.titulo-segundo
      h2 1.3   Constitucin poltica de Colombia

    p El pueblo de Colombia, en ejercicio de su poder soberano, representado por sus delegatarios a la Asamblea Nacional Constituyente, invocando la proteccin de Dios, y con el fin de fortalecer la unidad de la Nacin y asegurar a sus integrantes la vida, la convivencia, el trabajo, la justicia, la igualdad, el conocimiento, la libertad y la paz, dentro de un marco jurdico, democrtico y participativo que garantice un orden poltico, econmico y social justo, y comprometido a impulsar la integracin de la comunidad latinoamericana, decreta, sanciona y promulga la Constitucin poltica de Colombia de 1991 vigente hasta la actualidad. 
    .row.d-none.d-lg-block
      .row.justify-content-center.align-items-center.p-4.tarjeta_Sp-5
        .col-lg-6    
          p.mb-3  A partir de lo preceptuado en el  Artculo 93 de la Constitucin de 1991, la jurisprudencia  de la Corte Constitucional  construy el concepto  de bloque de constitucionalidad,  segn el cual en el ordenamiento  jurdico  deben prevalecer  los tratados  internacionales  que  versan sobre derechos  humanos cuya  limitacin  est prohibida en los Estados de excepcin,  como los convenios de la OIT  y las recomendaciones de sus rganos de control. ((Corte Constitucional (como se cit en La libertad sindical en el mundo del trabajo en Colombia, 2017)).

          p.mb-3 En consecuencia, la fuente  principal  del  ordenamiento  laboral colombiano es  la  Constitucin  Poltica,  que  instituye  en  su  Artculo  2  que  los  fines  esenciales del  Estado son proteger  la  vida, la  honra, los bienes,  las creencias  y libertades de las personas residentes en Colombia. Ms adelante seala:  Artculo 38, los derechos  de asociacin; Artculo  39, el  derecho  de asociacin  sindical  y reconocimiento  sin intervencin  del  Estado;  Artculo  53, los derechos  de  los trabajadores; Artculo  55,  negociacin  colectiva;  Artculo  56,  derecho  de  huelga;  Artculo  57, participacin de los trabajadores en la gestin de empresa.



        .col-lg-6.mb-5
          img(src="@/assets/curso/images/tema1/img_4.svg" alt="", style="width:450px; display:block; margin: 0 auto;")
    .row.d-none.d-lg-block
      .row.justify-content-center.align-items-center.p-4.mt
        .col-lg-6.mb-5
          img(src="@/assets/curso/images/tema1/img_5.svg" alt="", style="width:450px; display:block; margin: 0 auto;")
        .col-lg-6

          p.mb-3 De lo anterior se deduce que el derecho del mundo del trabajo en Colombia debe ser ledo, interpretado  y analizado  a partir de las Cartas sociales de derechos humanos del Sistema de Naciones Unidas, que incluye  a la OIT  y al Sistema Interamericano de Derechos Humanos. Como  exigencia  democrtica,  la  Constitucin  Poltica  de  Colombia  consagra  la  libertad  de las  organizaciones  sindicales.  Solo as  se logra  compensar  y eliminar  la desigualdad  social, mediante  el ejercicio  del derecho de asociacin sindical. 

          p.mb-3 En tal sentido, el derecho del trabajo debe expulsar la larga sombra de la codificacin,  para  entrar  a  un  nuevo  derecho  laboral  con  justicia  social. Si esta justicia no existe, el neoliberalismo congelar cada da ms las condiciones generales  de trabajo  y conformar  un sistema  laboral  complejo  pretendidamente ajeno  a la  justicia  social,  con claras  tendencias  a un sistema neocorporativo.  Sin embargo,  al  violentar  los  derechos  laborales,  desintegrar  los derechos  sociales, proscribir y aniquilar  los sindicatos y la autonoma  colectiva  del trabajo, no habr otra reaccin que la resistencia obrera y la lucha sindical.
   
