    #t_1_4.titulo-segundo
      h2 1.4   El trabajo como derecho humano fundamental
    
    
    .jumbotron-3.p-5.mb-5.d-none.d-lg-block
      .row.mb-5
        .col-lg-5.mb-5.mb-lg-0
          img(src="@/assets/curso/images/tema1/img_7.svg", alt= "",style="width:450px; display:block; margin:0 auto;") 
        .col-lg-7.mb-5.mb-lg-0
          p.mb-3 En primer lugar, se busca reconocer el trabajo como derecho fundamental, en su relacin con la satisfaccin de las necesidades bsicas humanas, el desarrollo de la persona y la consolidacin de la democracia. Socialmente se han diferenciado los espacios en los cules se realiza el trabajo y se les asigna distinto valor. El trabajo productivo, realizado por hombres en su mayora, los ha puesto en el lugar de proveedores gracias a la remuneracin recibida por el mismo, se ubica en el espacio pblico y las relaciones sociales, en correspondencia directa con la empresa o empleador/a. Por su parte, al trabajo reproductivo, casi siempre desempeado por mujeres, se le atribuye el espacio privado, el hogar y las actividades que corresponden a la crianza y cuidado de la familia y de las personas desprotegidas, ste trabajo, aunque aporta igual o mayor contribucin al bienestar social y econmico, no ha sido lo suficientemente valorado. 
          p (Didcticas para la ciudadana laboral, Escuela Nacional Sindical, 2011).
    
