    h4.mb-5 Dimensiones del trabajo
    
    .row.justify-content-center.align-items-center.mb-4
      .col-12.col-lg-6
        .bloque-texto-d.color-secundario.p-4
          .bloque-texto-d__texto.mb-3
            i.fas.fa-quote-left
            h3.text-regular El trabajo tiene una dimensin objetiva como accin y producto, tambin subjetiva como proyecto y reconocimiento social
            i.fas.fa-quote-right
          .bloque-texto-d__autor 
            .h5.mb-0 Cardona y Vera, 2003
            
        p.my-5 Las relaciones que tejen las personas con respecto al trabajo, son el sustento de la configuracin de la identidad colectiva e individual, las personas identifican su rol social en comparacin con su quehacer, es as como se hace la divisin social del trabajo. Por este motivo, el trabajo tambin es eje estructurante de jerarquas y poderes. La relacin trabajo/capital y el valor de cambio, tiene estrecha relacin con el acceso a esferas como la cultura, la educacin y la satisfaccin de necesidades, esto significa que la remuneracin por el trabajo realizado mejora la calidad de vida y que la valoracin social y econmica del mismo dignifica la vida de las personas trabajadoras.      

      .col-8.col-md-8.col-lg-6
        img(src="@/assets/curso/images/tema1/img_8.svg" alt="", style="width:450px; display:block; margin: 0 auto;")
      .row.justify-content-center.align-items-center
        .col-10
          .jumbotron-4.p-3
            .row.justify-content-center.align-items-center
              .col-8.col-md-8.col-lg-2
                img(src="@/assets/curso/images/tema1/ico_2.svg" alt="", style="width:200px; display:block; margin: 0 auto;")
              .col-8.col-md-8.col-lg-8
                p <b>Por tanto, las diferencias entre trabajo y empleo pueden conceptuarse como:</b> el trabajo transforma recursos y les da valor social, el empleo se mezcla en la relacin con el capital, es decir, ya no es una produccin social de transformacin, sino de flujo de capitales. El/la empleado/a tiene un rol determinado de su quehacer social, el trabajo por su parte es una accin que es o no remunerada, mientras el empleo est inmerso en una relacin contractual, el trabajo es la posibilidad de creacin y transformacin de todas las personas.
     
    .row.justify-content-center.align-items-center.mb-4
      .col-auto.onTop
        img(src="@/assets/curso/images/tema1/img_9.svg" alt='AvatarTop', style="width:450px;").m-auto 
      .col-lg
        .custom_box_left.mb-5
          p El sistema econmico capitalista de libre mercado ha cambiado el trabajo, desde la flexibilizacin laboral, deja de ser el esfuerzo aunado para el logro de un objetivo de desarrollo econmico y social, y se convierte en la bsqueda individual por la supervivencia. La precariedad laboral elimina los derechos fundamentales y despoja al trabajo de su potencia para la autorrealizacin y la emancipacin propia de la persona trabajadora. El capitalismo ha convertido a los/as trabajadores/as en seres desprotegidos social, econmica y culturalmente con manifestaciones como el subempleo y la informalidad.
