    #t_1_5.titulo-segundo
      h2 1.5 Los derechos del trabajo y en el trabajo   


    .jumbotron-5    
    
      .row.justify-content-center.align-items-center.p-4
        .col-lg-6.order-2.order-lg-1  
          p La relacin del derecho al trabajo con la vida social, econmica, poltica y cultural de las personas constituye el ncleo de desarrollo de su autonoma y en razn de esto el trabajo puede ser definido como un derecho fundamental, integrante de los derechos de segunda generacin denominados Derechos Econmicos, Sociales y Culturales (DESC) Desde mediados de 1980 se inicia en Colombia, como en otras partes del mundo, el desmonte paulatino del modelo de trabajo protegido. Este esquema surgido luego de la segunda guerra mundial en el marco de una poltica proteccionista y de expansin del mercado interno, incentiva el desarrollo de la industria manufacturera colombiana.

          p Didcticas para la ciudadana laboral, Escuela Nacional Sindical, 2011).

        .col-lg-6.mb-5.order-1.order-lg-2
          img(src="@/assets/curso/images/tema1/img_10.svg" alt="", style="width:450px; display:block; margin: 0 auto;")
      
      
      .row.justify-content-center.align-items-center.p-4
        .col-lg-6.mb-5
          img(src="@/assets/curso/images/tema1/img_11.png" alt="", style="width:450px; display:block; margin: 0 auto;")
        .col-lg-6
          p.mb-3 La proteccin del mercado interno, de sus industrias y del trabajo formal se caracteriza por la creacin de nuevas empresas y la promocin de su crecimiento, derivado de ello se produce la ampliacin del trabajo formal protegido y una proporcin de las personas en edad de trabajar encuentran un mecanismo de acceso a los derechos sociales: salud, pensin, vivienda, recreacin, que as mismo significa trayectorias laborales estables y de larga duracin.

          p.mb-3 Si bien este tipo de empresas absorbe slo a una parte modesta de la poblacin econmicamente activa, su existencia se asocia a la configuracin de un ideal de trabajo estable y bien remunerado. Tal ideal tambin es el referente de los empleados directos del Estado. Desde mediados de 1980 la posibilidad de acceder a ese derecho es cada vez ms incierta, como resultado del aumento del subempleo, la subcontratacin y la desregulacin laboral.

          p Las dinmicas econmicas recientes ligadas a mercados abiertos y altamente competidos son el marco en que cambia el mundo del trabajo, donde el predominio del mercado como mecanismo de coordinacin social obedece a un cambio poltico estructural en el que el estado social y de derecho pierde vigencia y centralidad. Son economas con alta incidencia y peso del sector financiero.
    
